<template>
  <div class="disaster">
    <div class="header_title"></div>
    <div
      v-for="item in contents_data1"
      :key="item.key"
    >
      <transition name="fade">
        <div
          v-show="showKey == item.key"
          class="content-container"
        >
          <div class="earthquake-first fade-in">
            <h2 class="unit_title">{{ item.unit_title }}</h2>
            <div class="subtitle">{{ item.subtitle }}</div>
            <ul
              v-for="(it, i) in item.data"
              :key="`data_${i}`"
            >
              <li
                class="introduction"
                :class="{ content_title: it.content_title }"
              >{{ it.content }}</li>
            </ul>
            <div class="progressbar"></div>
          </div>
          <img :src="require(`../img/earthquake_${item.key}.png`)">
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { ref, reactive } from 'vue';

export default {
  name: "Disaster",

  setup () {
    const contents_data1 = reactive(
      [
        {
          key: 1,
          unit_title: '',
          subtitle: '1.',
          data: [
            { content: "1974" },
            { content: "1974" },
            { content: "19995" },
            { content: "" },
            { img: "../img/earthquake_1.png" }
          ]
        },
        {
          key: 2,
          subtitle: '2.',
          data: [
            { content: "", content_title: true },
            { content: "4~5" },
            { content: "", content_title: true },
            { content: "" },
            { content: "", content_title: true },
            { content: "" },
            { content: "" },
            { img: "../img/earthquake_2.png" }
          ]
        },
        {
          key: 3,
          subtitle: '3.',
          data: [
            { content: "" },
            { content: "" },
            { img: "../img/earthquake_3.png" }
          ]
        },
        {
          key: 4,
          subtitle: '4.',
          data: [
            { content: "" },
            { content: "" },
            { content: "" },
            { img: "../img/earthquake_4.png" }
          ]
        },
        {
          key: 5,
          subtitle: '5.',
          data: [
            { content: "" },
            { content: "" },
            { content: "" },
            { img: "../img/earthquake_5.png" }
          ]
        }
      ]
    );
    let scrollNumUp = 0;
    let scrollNumDown = 0;
    const showKey = ref(1);
    let timer = false;

    window.addEventListener('wheel', (e) => {
      if (timer === true) {
        return;
      }
      const delta = Math.sign(e.deltaY);
      const range = Math.abs(e.deltaY);
      if (delta === 1) {
        scrollNumUp += range
        if (scrollNumUp > 180 && showKey.value > 1) {
          showKey.value--;
          scrollNumUp = 0;
          timer = true;
          setTimeout(() => {
            timer = false;
          }, 1000)
        }
      } else {
        scrollNumDown += range
        if (scrollNumDown > 180 && showKey.value < 5) {
          showKey.value++;
          scrollNumDown = 0;
          timer = true;
          setTimeout(() => {
            timer = false;
          }, 1000)
        }
      }
    })

    return {
      contents_data1,
      showKey
    }
  }
}
</script>

